<template>
  <ion-page>
    <ion-header mode="ios">
      <ion-toolbar>
        <ion-title>Tambah Soal</ion-title>
        <ion-back-button slot="start" default-href="/" />
      </ion-toolbar>
    </ion-header>
    <ion-content :fullscreen="true">
      <ion-refresher slot="fixed" @ionRefresh="doRefresh($event)">
        <ion-refresher-content />
      </ion-refresher>
      <ion-content class="ion-padding">
        <ion-item>
          <ion-label>Mata Pelajaran</ion-label>
          <ion-select
            v-model="selectedSubject"
            placeholder="Pilih Mata Pelajaran"
          >
            <ion-select-option
              v-for="subject in subjects"
              :key="subject"
              :value="subject"
            >
              {{ subject }}
            </ion-select-option>
          </ion-select>
        </ion-item>
        <ion-item>
          <ion-label>Kelas</ion-label>
          <ion-select v-model="selectedClass" placeholder="Pilih Kelas">
            <ion-select-option
              v-for="kelas in classes"
              :key="kelas"
              :value="kelas"
            >
              {{ kelas }}
            </ion-select-option>
          </ion-select>
        </ion-item>
        <ion-item>
          <ion-label>Soal</ion-label>
          <ion-textarea
            v-model="questionText"
            placeholder="Masukkan soal"
          ></ion-textarea>
        </ion-item>
        <ion-item>
          <ion-label>Pilihan A</ion-label>
          <ion-input
            v-model="optionA"
            placeholder="Masukkan pilihan A"
          ></ion-input>
        </ion-item>
        <ion-item>
          <ion-label>Pilihan B</ion-label>
          <ion-input
            v-model="optionB"
            placeholder="Masukkan pilihan B"
          ></ion-input>
        </ion-item>
        <ion-item>
          <ion-label>Pilihan C</ion-label>
          <ion-input
            v-model="optionC"
            placeholder="Masukkan pilihan C"
          ></ion-input>
        </ion-item>
        <ion-item>
          <ion-label>Pilihan D</ion-label>
          <ion-input
            v-model="optionD"
            placeholder="Masukkan pilihan D"
          ></ion-input>
        </ion-item>
        <ion-item>
          <ion-label>Pilihan E</ion-label>
          <ion-input
            v-model="optionE"
            placeholder="Masukkan pilihan E"
          ></ion-input>
        </ion-item>
        <ion-item>
          <ion-label>Jawaban Benar</ion-label>
          <ion-select v-model="correctAnswer" placeholder="Pilih Jawaban Benar">
            <ion-select-option value="A">A</ion-select-option>
            <ion-select-option value="B">B</ion-select-option>
            <ion-select-option value="C">C</ion-select-option>
            <ion-select-option value="D">D</ion-select-option>
            <ion-select-option value="E">E</ion-select-option>
          </ion-select>
        </ion-item>
        <ion-button expand="block" @click="addQuestion">Tambah Soal</ion-button>
        <ion-grid>
          <ion-row>
            <ion-col>
              <h2>Daftar Soal</h2>
              <ion-list>
                <ion-item>
                  <ion-label>
                    <table>
                      <thead>
                        <tr>
                          <th>Soal</th>
                          <th>Pilihan A</th>
                          <th>Pilihan B</th>
                          <th>Pilihan C</th>
                          <th>Pilihan D</th>
                          <th>Pilihan E</th>
                          <th>Jawaban Benar</th>
                          <th>Mata Pelajaran</th>
                          <th>Kelas</th>
                          <th>Aksi</th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr v-for="question in questions" :key="question.id">
                          <td>{{ question.question }}</td>
                          <td>{{ question.options.A }}</td>
                          <td>{{ question.options.B }}</td>
                          <td>{{ question.options.C }}</td>
                          <td>{{ question.options.D }}</td>
                          <td>{{ question.options.E }}</td>
                          <td>{{ question.correctAnswer }}</td>
                          <td>{{ question.subject }}</td>
                          <td>{{ question.class }}</td>
                          <td>
                            <ion-button
                              @click="editQuestion(question)"
                              color="warning"
                              >Edit</ion-button
                            >
                            <ion-button
                              @click="deleteQuestion(question.id)"
                              color="danger"
                              >Hapus</ion-button
                            >
                          </td>
                        </tr>
                      </tbody>
                    </table>
                  </ion-label>
                </ion-item>
              </ion-list>
            </ion-col>
          </ion-row>
        </ion-grid>
      </ion-content>
      <ion-toast
        :is-open="toast.isOpen"
        :message="toast.message"
        :duration="toast.duration"
        :color="toast.color"
        @did-dismiss="() => (toast.isOpen = false)"
      ></ion-toast>
      <ion-modal :is-open="isModalOpen" @did-dismiss="closeModal">
        <ion-header>
          <ion-toolbar>
            <ion-title>Edit Soal</ion-title>
            <ion-buttons slot="end">
              <ion-button @click="closeModal">Close</ion-button>
            </ion-buttons>
          </ion-toolbar>
        </ion-header>
        <ion-content>
          <ion-item>
            <ion-label>Mata Pelajaran</ion-label>
            <ion-select
              v-model="editSubject"
              placeholder="Pilih Mata Pelajaran"
            >
              <ion-select-option
                v-for="subject in subjects"
                :key="subject"
                :value="subject"
              >
                {{ subject }}
              </ion-select-option>
            </ion-select>
          </ion-item>
          <ion-item>
            <ion-label>Kelas</ion-label>
            <ion-select v-model="editClass" placeholder="Pilih Kelas">
              <ion-select-option
                v-for="kelas in classes"
                :key="kelas"
                :value="kelas"
              >
                {{ kelas }}
              </ion-select-option>
            </ion-select>
          </ion-item>
          <ion-item>
            <ion-label>Soal</ion-label>
            <ion-textarea
              v-model="editQuestionText"
              placeholder="Masukkan soal"
            ></ion-textarea>
          </ion-item>
          <ion-item>
            <ion-label>Pilihan A</ion-label>
            <ion-input
              v-model="editOptionA"
              placeholder="Masukkan pilihan A"
            ></ion-input>
          </ion-item>
          <ion-item>
            <ion-label>Pilihan B</ion-label>
            <ion-input
              v-model="editOptionB"
              placeholder="Masukkan pilihan B"
            ></ion-input>
          </ion-item>
          <ion-item>
            <ion-label>Pilihan C</ion-label>
            <ion-input
              v-model="editOptionC"
              placeholder="Masukkan pilihan C"
            ></ion-input>
          </ion-item>
          <ion-item>
            <ion-label>Pilihan D</ion-label>
            <ion-input
              v-model="editOptionD"
              placeholder="Masukkan pilihan D"
            ></ion-input>
          </ion-item>
          <ion-item>
            <ion-label>Pilihan E</ion-label>
            <ion-input
              v-model="editOptionE"
              placeholder="Masukkan pilihan E"
            ></ion-input>
          </ion-item>
          <ion-item>
            <ion-label>Jawaban Benar</ion-label>
            <ion-select
              v-model="editCorrectAnswer"
              placeholder="Pilih Jawaban Benar"
            >
              <ion-select-option value="A">A</ion-select-option>
              <ion-select-option value="B">B</ion-select-option>
              <ion-select-option value="C">C</ion-select-option>
              <ion-select-option value="D">D</ion-select-option>
              <ion-select-option value="E">E</ion-select-option>
            </ion-select>
          </ion-item>
          <ion-button expand="block" @click="updateQuestion"
            >Simpan Perubahan</ion-button
          >
        </ion-content>
      </ion-modal>
    </ion-content>
  </ion-page>
</template>
<script setup>
import {
  IonPage,
  IonHeader,
  IonToolbar,
  IonTitle,
  IonBackButton,
  IonContent,
  IonRefresher,
  IonRefresherContent,
  IonItem,
  IonLabel,
  IonSelect,
  IonSelectOption,
  IonTextarea,
  IonInput,
  IonButton,
  IonToast,
  IonList,
  IonModal,
  IonButtons,
} from "@ionic/vue";
import { ref, onMounted } from "vue";
import {
  getFirestore,
  collection,
  addDoc,
  getDocs,
  updateDoc,
  doc,
  deleteDoc,
} from "firebase/firestore";
import { firebaseApp } from "../firebase.js";

const db = getFirestore(firebaseApp);

const subjects = [
  "Matematika",
  "Bahasa Indonesia",
  "Bahasa Inggris",
  "IPA",
  "IPS",
];
const classes = [
  "Kelas 1",
  "Kelas 2",
  "Kelas 3",
  "Kelas 4",
  "Kelas 5",
  "Kelas 6",
];

const selectedSubject = ref("");
const selectedClass = ref("");
const questionText = ref("");
const optionA = ref("");
const optionB = ref("");
const optionC = ref("");
const optionD = ref("");
const optionE = ref("");
const correctAnswer = ref("");
const questions = ref([]);

const isModalOpen = ref(false);
const editSubject = ref("");
const editClass = ref("");
const editQuestionText = ref("");
const editOptionA = ref("");
const editOptionB = ref("");
const editOptionC = ref("");
const editOptionD = ref("");
const editOptionE = ref("");
const editCorrectAnswer = ref("");
const editQuestionId = ref("");

const toast = ref({
  isOpen: false,
  message: "",
  duration: 2000,
  color: "success",
});

const fetchQuestions = async () => {
  const querySnapshot = await getDocs(collection(db, "soal"));
  questions.value = querySnapshot.docs.map((doc) => ({
    id: doc.id,
    ...doc.data(),
  }));
};

const addQuestion = async () => {
  if (
    !selectedSubject.value ||
    !selectedClass.value ||
    !questionText.value ||
    !optionA.value ||
    !optionB.value ||
    !optionC.value ||
    !optionD.value ||
    !optionE.value ||
    !correctAnswer.value
  ) {
    showToast("Semua kolom harus diisi", "danger");
    return;
  }

  try {
    await addDoc(collection(db, "soal"), {
      subject: selectedSubject.value,
      class: selectedClass.value,
      question: questionText.value,
      options: {
        A: optionA.value,
        B: optionB.value,
        C: optionC.value,
        D: optionD.value,
        E: optionE.value,
      },
      correctAnswer: correctAnswer.value,
    });
    showToast("Soal berhasil ditambahkan", "success");
    resetForm();
    fetchQuestions(); // Fetch updated questions after adding a new one
  } catch (error) {
    console.error("Error adding question:", error);
    showToast("Terjadi kesalahan saat menambahkan soal", "danger");
  }
};

const editQuestion = (question) => {
  editQuestionId.value = question.id;
  editSubject.value = question.subject;
  editClass.value = question.class;
  editQuestionText.value = question.question;
  editOptionA.value = question.options.A;
  editOptionB.value = question.options.B;
  editOptionC.value = question.options.C;
  editOptionD.value = question.options.D;
  editOptionE.value = question.options.E;
  editCorrectAnswer.value = question.correctAnswer;
  isModalOpen.value = true;
};

const updateQuestion = async () => {
  if (
    !editSubject.value ||
    !editClass.value ||
    !editQuestionText.value ||
    !editOptionA.value ||
    !editOptionB.value ||
    !editOptionC.value ||
    !editOptionD.value ||
    !editOptionE.value ||
    !editCorrectAnswer.value
  ) {
    showToast("Semua kolom harus diisi", "danger");
    return;
  }

  try {
    await updateDoc(doc(db, "soal", editQuestionId.value), {
      subject: editSubject.value,
      class: editClass.value,
      question: editQuestionText.value,
      options: {
        A: editOptionA.value,
        B: editOptionB.value,
        C: editOptionC.value,
        D: editOptionD.value,
        E: editOptionE.value,
      },
      correctAnswer: editCorrectAnswer.value,
    });
    showToast("Soal berhasil diperbarui", "success");
    isModalOpen.value = false;
    fetchQuestions(); // Fetch updated questions after editing a question
  } catch (error) {
    console.error("Error updating question:", error);
    showToast("Terjadi kesalahan saat memperbarui soal", "danger");
  }
};

const deleteQuestion = async (questionId) => {
  try {
    await deleteDoc(doc(db, "soal", questionId));
    showToast("Soal berhasil dihapus", "success");
    fetchQuestions(); // Fetch updated questions after deleting a question
  } catch (error) {
    console.error("Error deleting question:", error);
    showToast("Terjadi kesalahan saat menghapus soal", "danger");
  }
};

const resetForm = () => {
  selectedSubject.value = "";
  selectedClass.value = "";
  questionText.value = "";
  optionA.value = "";
  optionB.value = "";
  optionC.value = "";
  optionD.value = "";
  optionE.value = "";
  correctAnswer.value = "";
};

const showToast = (message, color) => {
  toast.value = {
    isOpen: true,
    message: message,
    duration: 2000,
    color: color,
  };
};

const doRefresh = (event) => {
  setTimeout(() => {
    fetchQuestions();
    event.target.complete();
  }, 2000);
};

const closeModal = () => {
  isModalOpen.value = false;
};

onMounted(() => {
  fetchQuestions();
});
</script>
